
import React from 'react';
import Typed from 'typed.js';

export default class TypedComp extends React.Component {

  componentDidMount() {
    const { strings } = this.props;

    const options = {
    	strings: strings,
      typeSpeed: 50,
      backSpeed: 50,
      startDelay: 1000,
      cursorChar: '_',
      loop: false
    };

    // this.el refers to the <span> in the render() method
    this.typed = new Typed(this.el, options);
  }

  componentWillUnmount() {
    // to prevent memory leaks
    this.typed.destroy();
  }

  render() {
    return (
      <div className="typed">
        <span
          style={{ whiteSpace: 'pre' }}
          ref={(el) => { this.el = el; }}
        />
      </div>
    );
  }
}
