import React from 'react';
import Project from './project'
import ProjectData from './projectData'
import Tools from './tools'
export default function Body() {

const projectComponent = ProjectData.map(single => <Project key={single.id} imgUrl={single.imgUrl} title={single.title} description={single.description} linkUrl={single.linkUrl} gitUrl={single.gitUrl} />);

return (
  <div id='body'>
    <div className='projects'>
      {projectComponent}
      <Tools />
    </div>
  </div>
)
}
